import React, { useState } from 'react';
import styles from './ResponseDisplay.module.css'; // Your existing CSS module

const ResponseDisplay = ({ dnsResponse }) => {
  const [collapsedSections, setCollapsedSections] = useState({}); // State to track collapsed sections

  const toggleCollapse = (section) => {
    setCollapsedSections(prevState => ({
      ...prevState,
      [section]: !prevState[section]
    }));
  };

  const renderSection = (key, data) => {
    return (
      <div key={key} className={styles.recordSection}>
        <h5 onClick={() => toggleCollapse(key)} className={styles.recordTitle}>{key}</h5>
        {!collapsedSections[key] && (
          <pre className={styles.recordData}>{JSON.stringify(data, null, 2)}</pre>
        )}
      </div>
    );
  };

  return (
    <div className={styles.responseContainer}>
      <div className={styles.responseCard}>
        {dnsResponse ? (
          <div>
            <h5 className={styles.responseTitle}>Results:</h5>
            {Object.keys(dnsResponse).map(key => renderSection(key, dnsResponse[key]))}
          </div>
        ) : (
          <p className={styles.noResultsText}>No results to display</p>
        )}
      </div>
    </div>
  );
};

export default ResponseDisplay;
